<template>
  <v-container class="py-8">
    <v-card class="pa-4" elevation="2">
      <h2 class="mb-4">Информация о запуске</h2>

      <v-list density="compact">
        <v-list-item>
          <v-list-item-title><strong>Бот:</strong> {{ bot ?? 'данные отсутствуют' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>ID пользователя:</strong> {{ user?.id ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Имя:</strong> {{ user?.first_name ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Телефон:</strong> {{ phoneText }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>Идентификатор чата:</strong> {{ chat?.id ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>	Тип чата:</strong> {{ chat?.type ?? '-' }}</v-list-item-title>
        </v-list-item>

        <v-list-item>
          <v-list-item-title><strong>initDataUnsafe:</strong></v-list-item-title>
        </v-list-item>

        <pre style="font-size: 12px; white-space: pre-wrap;">
          {{ initDump }}
        </pre>

        <v-list-item>
          <v-list-item-title><strong>initData:</strong> </v-list-item-title>
        </v-list-item>

        <pre style="font-size: 12px; white-space: pre-wrap;">
          {{ initData }}
        </pre>
      </v-list>
    </v-card>
  </v-container>
</template>

<script>
import { ref, onMounted } from "vue";
import { computed } from 'vue';

export default {
  setup() {
    const bot = ref(null);
    const user = ref(null);
    const phone = ref(null);
    const chat = ref(null)
    const initData = ref(null)
    const initDump = ref(null);
    const requestingPhone = ref(false);

    const phoneText = computed(() => {
      if (phone.value) return phone.value;
      if (requestingPhone.value) return 'запрашиваю...';
      return 'не получен';
    });

    onMounted(() => {
      const init = window.WebApp?.initDataUnsafe;

      if (init) {
        bot.value = init.query?.bot ?? null;
        user.value = init.user ?? null;
        chat.value = init.chat ?? null;
        initData.value = JSON.stringify(init, null, 2);
        initDump.value = JSON.stringify(init, null, 2);

        // если телефон пришел сразу
        if (init.user?.phone) {
          phone.value = init.user.phone;
        } else {
          // иначе запрашиваем
          requestingPhone.value = true;
          window.WebApp.requestContact()
            .then(res => {
              phone.value = res.phone;
              requestingPhone.value = false;
            })
            .catch(() => {
              requestingPhone.value = false;
            });
        }
      }

      // debug
      console.log("WebApp.initDataUnsafe =", init);
    });

    return {
      bot,
      user,
      phone,
      phoneText
    };
  }
};
</script>
